.backdroplayer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 20;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.726);
}

.modaloverlay {
     
     margin:auto;
    position: fixed;
    top: 5vh;
    left: 5vw;
    width: 80vw;
    height:80vh;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
    overflow: hidden;
  }

  @keyframes slide-down {
    from {
      opacity: 0;
      transform: translateY(-3rem);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }